document.addEventListener('DOMContentLoaded', function() {  
  //alert('');   
  // console.log(' !');


  //swiper - Hero
  const swiperOhero = document.querySelector(".hero")
  //  if
  if (swiperOhero) {// if.  Gulp-  null    
    const heroSwiper = new Swiper(".hero__swiper", {  
    slidesPerView: .49,
    centeredSlides: false,
    slidesPerGroupSkip: 1,
    grabCursor: true,
    keyboard: {
      enabled: true,
    },
    breakpoints: {
      769: {
        slidesPerView: 1,
      },
    },
    scrollbar: {
      el: ".swiper-scrollbar",
    }
    // navigation: {
    //   nextEl: ".swiper-button-next",
    //   prevEl: ".swiper-button-prev",
    // },
    // pagination: {
    //   el: ".swiper-pagination",
    //   clickable: true,
    // },
  });
};


  //swiper - Post
  const swiperOpost = document.querySelector(".post")
  //  if
  if (swiperOpost) {// if.  Gulp-  null      
    const postSwiper = new Swiper(".post__swiper", {
      pagination: {
        el: ".post__pagination",
        clickable: true,
      },
      breakpoints: {
        320: {
          slidesPerView:1,
          spaceBetween: 32,
        },

        500: {
          slidesPerView:2,
          spaceBetween: 32,
        },

        700: {
          slidesPerView:3,
          spaceBetween: 32,
        }},

      //  
      speed: 3000,// 

      autoplay: {
        delay: 5000,// 
        disableOnInteraction: false,      
      },

    });
  }; 




});


